.display1{
  font-size: 2.375em;
  line-height: 130%;
}

.about h2,
.display2{
  font-size: 1.875em;
  line-height: 130%;
}

.about h3,
.article-chapter h3,
.display3{
  font-size: 1.375em;
  line-height: 130%;
}

.display4{
  font-size: 1.375em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
  line-height: 145%;
}

.article-chapter h5,
.article-chapter p,
.article-chapter ul,
.article-chapter ol,
.about blockquote p,
.text17{
  font-size: 1.0625em;
  line-height: 150%;
}

.text16{
  font-size: 1em;
  line-height: 125%;
}

.text15{
  font-size: .9375em;
}

.text14{
  font-size: .875em;
  line-height: 140%;
}

.text11{
  font-size: .6875em;
}

.text9{
  font-size: .5625EM;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-weight-text{
  font-weight: 500;
}

.light-weight-text{
  font-weight: 300;
}

.accent-text{
  color: $accent;
}

.accent2-text{
  color: $accent2;
}

.red-text{
  color: $red;
}

.green-text{
  color: $green;
}

.grey-text{
  color: $grey;
}

.grey-text2{
  color: $grey2;
}

.light-grey-text{
  color: $lightGrey;
}

.link-text{
  color: $linkColor;
}

.font2{
  font-family: $font2;
}

.article-chapter ul{
  padding-left: .65em;
  li{
    line-height: inherit;
    display: flex;
    &:before{
      content: '';
      width: .2em;
      height: .2em;
      display: block;
      flex-shrink: 0;
      border-radius: 50%;
      background-color: $black;
      margin-right: .65em;
      transform: translateY(.67em);
    }
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 2em;
  }

  .about h2,
  .display2{
    font-size: 1.625em;
  }

  .display3{
    font-size: 1.25em;
  }

  .mob-text15{
    font-size: .9375em;
  }
}
