@import "../mixins";
.search{
  &__items{
    @include grid100gap(1.75em);
    margin-top: 2em;
  }
  .blog__info2{
    padding: 0;
  }
  .blog__title{
    @media screen and (min-width: 577px){
      text-transform: uppercase;
    }
  }
}

.search-card{
  display: grid;
  grid-template-columns: 17.8125em calc(100% - 17.8125em);
  background: #FFFFFF;
  box-shadow: 1px 1px 31px rgba(43, 42, 124, 0.07);
  &__img{
    display: block;
    position: relative;
    padding-bottom: 75%;
    min-height: 100%;
    img{
      @include imgCover;
    }
  }
  &__content{
    padding: .75em 1.75em;
  }
  &__info,
  &__text{
    @include grid100gap(.25em);
  }
  &__text{
    p{
      line-height: inherit;
    }
    span{
      color: $linkColor;
    }
  }
}


@media screen and (max-width: 576px){
  .search{
    .blog__info2{
      gap: 1em;
    }
  }

  .search-card{
    grid-template-columns: 100%;
    &__img{
      padding-bottom: 57%;
    }
    &__content{
      padding: 1.25em 1em 1em;
    }
  }
}
