@import "../vars";
@import "../mixins";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-size: 1em;
    &:focus {
        box-shadow: none;
    }
    svg,
    img{
        display: block;
        width: 1.5em;
        height: auto;
        &:first-child{
            margin-right: .625em;
        }
        &:nth-child(2){
            margin-left: .625em;
        }
    }
}

.m-btn-icon{
    img,svg{
        margin: 0 !important;
    }
}

.m-btn48{
    height: 3em;
    border-radius: .5em;
}

.m-btn50{
    height: 3.125em;
    border-radius: .3125em;
    span{
        font-size: 1.125em;
    }
}

.m-btn38{
    height: 2.375em;
    border-radius: .3125em;
    span{
        font-size: .875em;
    }
}

.m-btn-accent2{
    background-color: $accent2;
    color: #fff;
    padding: 0 1em;
    font-weight: 600;
    border-radius: .3125em;
    height: 2.125em;
    span{
        font-size: .875em;
    }
    &:hover{
        background-color: #5B58DC;
        color: #fff;
    }
}

.m-btn-black{
    padding: 0 1.5em;
    background-color: $secondary3;
    color: #FFFFFF;
    &:hover{
        background-color: #585A5F;
        color: #FFFFFF;
    }
}

blockquote{
    background-image: $linear2;
    position: relative;
    border-radius: .3125em;
    &:before{
        content: '';
        position: absolute;
        @include absolute;
        width: calc(100% - 4px);
        height: calc(100% - 4px);
        background-color: #fff;
        border-radius: inherit;
    }
}

.linear-btn{
    position: relative;
    border-radius: .3125em;
    &:before{
        content: '';
        position: absolute;
        @include absolute;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        border: 2px solid $accent2;
    }
}

.linear-btn{
    font-weight: 600;
    color: $accent2;
    &.m-btn38{
        height: 2.375em;
        border-width: 1px;
    }

    span{
        position: relative;
        z-index: 2;
    }
    &:hover{
        color: #fff;
        background-image: $linear2;
        &:before{
            opacity: 0;
        }
    }
}


.accent-link,
.link-linear{
    font-size: 1.25em;
    line-height: 130%;
    text-decoration: underline;
    font-weight: 500;
}

.accent-link{
    color: $accent;
}

.link-linear{
    position: relative;
    z-index: 2;
    background-image: $linear2;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    &:hover{
        background-image: $linear;
    }
}

.accent-link2{
    text-decoration: underline;
    color: $accent2;
}


.m-btn-light-gray{
    border-radius: .3125em;
    background-color: $lightGrey;
    color: $black;
    &:hover{
        background-color: darken($lightGrey,10%);
        color: $black;
    }
}
