@import "../mixins";
.footer{
  padding: 1em 0;
  background-color: $secondary3;
  color: #fff;
  &__inner{
    padding-right: 11.5625em;
    position: relative;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__c{
    max-width: 30.5em;
    line-height: 120%;
    font-family: $font3;
    .text17{
      line-height: inherit;
    }
  }
  &__link{
    padding: 0 1em;
    height: 2.75em;
    font-size: 1em;
    font-weight: 500;
    img{
      width: 1.5em;
    }
    &:hover{
      color: #fff;
      background-color: #5F6472;
    }
  }
  &__bottom{
    margin-top: .5em;
  }
  &__links2{
    display: flex;
    justify-content: space-between;
  }
  &__link2{
    font-size: .9375em;
    line-height: 150%;
    text-decoration-line: underline;
  }
  &__btn{
    position: absolute;
    right: 0;
    top: 0;
    padding: 0 .9375em;
    height: 3.375em;
  }
}

.scroll-up{
  position: fixed;
  bottom: 10em;
  right: 3.75em;
  width: 2.75em;
  height: 2.75em;
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
  z-index: 10;
  background-color: transparent;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  svg{
    width: 100%;
    height: auto;
    margin: 0 !important;
  }
  &:hover{
    .stop1{
      stop-color: #2039BD;
    }
    .stop2{
      stop-color: #D01162;
    }
  }
}



@media screen and (max-width: 992px){
  .footer{
    padding-bottom: 2em;
    &__inner{
      padding: 0;
    }
    &__top{
      flex-direction: column;
      align-items: center;
    }
    &__link{
      margin-right: auto;
      margin-left: -1em;
    }
    &__c{
      order: 2;
      margin-top: .5em;
      margin-right: auto;
    }
    &__bottom{
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-top: 1em;
    }
    &__links2{
      @include grid100gap(.375em);
    }
    &__link2{
      font-size: .75em;
    }
    &__btn{
      position: static;
    }
  }

  .scroll-up{
    right: 1.25em;
    bottom: 1.25em;
  }
}
