@import "../mixins";
.profile{
  display: flex;
  justify-content: space-between;
  margin-top: -3.5em;
  &__wrap{
    display: flex;
    align-items: center;
  }
  .avatar{
    width: 7.5em;
    height: 7.5em;
    margin-right: 1.75em;
  }
  &__info{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__name{
    margin-right: auto;
  }
  .comment-card__status{
    margin: .375em auto 0 0;
  }
  &__nav{
    margin-left: auto;
    padding-left: 1em;
    flex-shrink: 0;
    display: flex;
    justify-content: flex-end;
  }
  &__logout{
    text-decoration: underline;
  }
  &__tabs{
    margin-top: 3.25em;
  }
  &__content{
    margin-top: 2em;
  }
}

.profile-comments{
  .comment-card{
    &__reactions{
      display: flex;
      align-items: center;
      .reactions__btn{
        margin-left: 1.25em;
        margin-right: 0;
        svg{
          width: 1.25em;
          height: 1.25em;
          margin-right: .5em;
        }
      }
      ._like{
        .grey-text{
          color: $green;
        }
      }
      ._unlike{
        .grey-text{
          color: $red;
        }
      }
    }
    &__title{
      margin-top: .5em;
      .text17{
        line-height: 140%;
      }
    }
    &__text{
      margin-top: .5em;
    }
    &__new{
      margin-top: .5em;
      &-text{
        color: $accent;
        display: flex;
        align-items: center;
        span{
          text-decoration: underline;
        }
        img{
          width: 1.25em;
          flex-shrink: 0;
          display: block;
          margin-right: .5em;
        }
      }
    }
  }
  .comment-subgroup{
    padding-top: 1.25em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: flex-end;
  }
  &__link{
    font-size: .875em;
  }
}

.favourite{
  &__items{
    @include grid100gap(2.5em);
  }
}

.favourite-card{
  background: #FFFFFF;
  border-radius: .3125em;
  padding: 1.25em 1.5em;
  min-height: 11.25em;
  display: grid;
  grid-template-columns: calc(100% - 12.5em) 11.25em;
  position: relative;
  &__img{
    position: relative;
    padding-bottom: 75%;
    img{
      @include imgCover;
    }
    &-wrap{
      position: absolute;
      top: 1.25em;
      right: 1.5em;
      width: 11.25em;
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  .avatar{
    width: 3em;
    height: 3em;
    margin-right: 1em;
    flex-shrink: 0;
  }
  &__info{
    &-bottom{
      margin-top: .25em;
      display: flex;
      align-items: center;
    }
    &-items{
      display: flex;
      align-items: center;
    }
    &-item{
      display: flex;
      align-items: center;
      margin-left: 1em;
      img{
        width: 1.25em;
        height: 1.25em;
        margin-right: .5em;
      }
    }
  }
  &__title{
    margin-top: .75em;
  }
  .reactions{
    margin-top: .75em;
    &__btn{
      margin-right: 1.25em;
      svg{
        width: 1.25em;
        height: 1.25em;
        margin-right: .5em;
      }
    }
  }
}

.subscribes{
  &__items{
    @include grid100gap(1.25em);
  }
  .profile-comments__nav{
    justify-content: unset;
  }
}

.subscribe-card{
  display: flex;
  align-items: center;
  border-radius: .375em;
  background-color: #fff;
  padding: .75em 1.25em;
  &__links{
    display: flex;
    align-items: center;
    margin-left: 1em;
  }
  &__link{
    margin-right: 1em;
  }
  &__link,
  &__text{
    font-weight: 300;
    font-family: $font2;
  }
  &__nav{
    margin-left: auto;
    padding-left: .5em;
    flex-shrink: 0;
  }
  &__text{
    margin-left: 1em;
  }
  &__cancel{
    text-decoration: underline;
    font-family: $font2;
  }
}

.send{
  padding: .75em 1.25em;
  border-radius: .325em;
  border: $border;
  margin-top: 3.75em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__mail{
    margin: 0 1em;
  }
  &__cancel{
    text-decoration: underline;
  }
}

.hints{
  margin-top: 5em;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.875em 3.125em;
}

.hint-card{
  padding: 2.75em 1.25em;
  text-align: center;
  @include card;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.5em;
  }
  &__btn{
    padding: 0 1.5em;
  }
}

.empty-comment{
  &__text{
    margin-top: 1.25em;
  }
  &__nav{
    display: flex;
    margin-top: 1.75em;
  }
  &__btn{
    padding: 0 1.5em;
  }
}


@media screen and (max-width: 992px){
  .profile{
    &__tabs{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .profile{
    margin-top: -1.25em;
    flex-direction: column-reverse;
    &__wrap{
      margin-top: 1.25em;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    .avatar{
      margin: 0 0 1em;
    }
    .comment-card__status{
      margin-left: auto;
    }
  }

  .profile-comments{
    .comment-card{
      position: relative;
      &__top{
        min-height: 3em;
        padding: .125em 0 .125em 4em;
        flex-wrap: wrap;
        justify-content: unset;
      }
      .avatar{
        position: absolute;
        left: 0;
        top: 0;
      }
      &__date{
        position: static;
        margin: 0;
      }
      &__name{
        width: 100%;
        max-width: unset;
        margin-bottom: .63em;
      }
      &__reactions{
        margin-left: 1em;
        .reactions__btn{
          margin: 0 1.25em 0 0 !important;
          svg{
            width: 1em;
            height: 1em;
          }
        }
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .favourite-card{
    grid-template-columns: 100%;
    @include fullWith;
    padding: 20px;
    &__img{
      &-wrap{
        width: 100%;
        position: static;
        margin: 1em 0;
      }
    }
  }

  .subscribes{
    &__items{
      @include grid100gap(.75em);
    }
  }

  .subscribe-card{
    padding: .75em 1em;
    flex-wrap: wrap;
    &__title{
      width: 100%;
      margin-bottom: .5em;
    }
    &__links,
    &__text{
      margin: 0;
    }
  }

  .send{
    @include grid100gap(.5em);
    padding: .8em 1.25em;
    &__mail{
      margin: 0;
    }
  }

  .hints{
    grid-template-columns: 100%;
    margin-top: 4em;
  }

  .hint-card{
    padding: 1.25em;
    &__nav{
      margin-top: .75em;
    }
    &__btn{
      width: 13.75em;
      max-width: 100%;
    }
  }

  .empty-comment{
    .display3{
      font-size: 1.125em;
    }
    &__text{
      margin-top: 1em;
    }
    &__btn{
      width: 100%;
    }
  }
}
