@import "../vars.scss";
.breadcrumbs {
    padding: 2.5em 0 1em;
    &__item {
        font-size: 1.0625em;
        line-height: 140%;
        position: relative;
        font-weight: 300;
        display: inline;
        color: $lightGrey;
        &:not(:first-child) {
            &:before {
                content: '-';
                font-weight: 700;
                margin: 0 .5em 0 .4em;
            }
        }
    }
}

@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
