@import "../mixins";
.about{
  &__desc{
    margin: 3.75em 0;
    @include grid100gap(1.25em);
    .text17{
      margin-top: 1.2em;
    }
  }
  h2{
    font-weight: 700;
    margin-bottom: 1.2em;
  }
  h3{
    font-weight: 600;
  }
  img{
    display: block;
    width: 100%;
  }
  &__wrap{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.25em;
    padding-bottom: 3.75em;
  }
  &__left{
    img{
      display: block;
      margin-bottom: .5em;
    }
    p{
      font-size: .6875em;
      line-height: 140%;
    }
  }
  &__right{
    ol{
      font-family: $font2;
      font-weight: 300;
    }
    li{
      line-height: inherit;
      ol{
        padding-left: 1em;
        font-size: 1em;
      }
    }
  }
  blockquote{
    margin: 3.75em 0;
    max-width: 43.1875em;
  }
}

.about-slider{
  padding-bottom: 3.75em;
  border-bottom: 2px solid $accent2;
  .swiper{
    &-container{
      margin-top: 1.75em;
      overflow-x: hidden;
      @include fullWith;
    }
    &-wrapper{
      margin-bottom: 1em;
    }
    &-slide{
      height: 10.5em;
      width: 16.875em;
      padding-right: 1em;
      img{
        @include imgCover;
        position: static;
      }
    }
    &-pagination{
      position: static;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100% !important;
      transform: unset !important;
      &-bullet{
        background-color: $lightGrey;
        opacity: 1;
        &-main{
          background-color: $grey;
        }
      }
    }
  }
}

.about-car{
  @include grid2;
  &__img{
    padding-bottom: 65%;
    position: relative;
    margin-bottom: 1em;
    img{
      @include imgCover;
    }
  }
}

.about-form{
  margin-top: 7.5em;
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.25em;
    margin-top: 2em;
    .fg,
    .captcha,
    .m-radio{
      &:not(:last-child){
        margin-bottom: 1em;
      }
    }
    textarea{
      height: unset !important;
      padding: .67em;
    }
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__btn{
    margin-top: auto;
    width: 100%;
  }
}

.captcha{
  display: flex;
  align-items: center;
  justify-content: center;
  img{
    width: 11.25em;
    display: block;
    margin-right: 1.75em;
  }
  &__btn{
    width: 2.5em;
    font-size: 1em;
    img{
      width: 100%;
    }
  }
}

.about-table{
  margin-top: 7.5em;
  overflow-x: auto;
  &__row{
    display: grid;
    grid-template-columns: calc(100% - 40em) 20em 20em;
    min-width: 49em;
    &:last-child{
      .about-table__col2{
        border-bottom: $border;
      }
    }
    &:nth-child(even){
      .about-table__col2{
        background-color: #fff;
        &:last-child{
          background-color: #F5F5F9;
        }
      }
    }
  }
  &__col2{
    padding: 1em;
    text-align: center;
    border-top: $border;
    border-left: $border;
    background-color: #F5F5F9;
    &:last-child{
      border-right: $border;
      background-color: #FFFFFF;
    }
  }
  &__col{
    display: flex;
    align-items: center;
  }
}

.about-hints{
  @include grid2;
  margin-top: 8.75em;
}

.about-hint{
  background: #D0E5F3;
  padding: 2.375em 2em 1.75em;
  &__desc{
    margin-top: 1.375em;
    max-width: 12.5em;
    position: relative;
    z-index: 2;
    &~.about-hint__img{
      margin-top: -3.125em;
    }
  }
  &__img{
    width: calc(100% + 4em);
    margin-left: -2em;
    margin-top: .75em;
    img{
      width: 100%;
    }
  }
  &__btn{
    width: 100%;
    margin-top: .125em;
    &:before{
      background: #D0E5F3;
    }
  }
}

.contacts{
  margin-top: 4.625em;
  position: relative;
  &__wrap{
    width: 25em;
    max-width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background: #FFFFFF;
    border-radius: .3125em;
    padding: 2em 2.5em;
    z-index: 2;
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    margin-right: 1em;
    width: 1.5em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
  &__hint{
    margin-top: 1.25em;
  }
}

.map{
  height: 25em;
  width: 100%;
  &__wrap{
    width: calc(100% + 4.5em);
    margin-left: -4.5em;
  }
}

.qa{
  margin-top: 6.25em;
  &__items{
    @include grid100gap(1.25em);
    margin-top: 2.75em;
  }
}

.qa-card{
  border: 1px solid $accent2;
  border-radius: .3125em;
  &._active{
    background-color: #fff;
    .qa-card__btn{
      transform: rotate(-180deg);
    }
    .qa-card__title{
      color: $accent2;
    }
  }
  &__show{
    padding: 1.5em 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    font-size: 1em;
    border: $border;
    border-radius: 50%;
    width: 2.25em;
    height: 2.25em;
    flex-shrink: 0;
    margin-left: .5em;
    @include center;
    img{
      width: 1em;
    }
  }
  &__hidden{
    padding: 1.75em 0;
    border-top: $border;
    width: calc(100% - 4em);
    margin: 0 auto;
    display: none;
  }
  &__text{
    max-width: 34em;
    margin: 0 auto;
    @include grid100gap(1.5em);
    p{
      line-height: inherit;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__desc{
      margin-top: 1.75em;
      gap: 2em;
      .text17{
        margin-top: 0;
      }
    }
    &__img{
      width: calc(100% + 2.5em) !important;
      margin-left: -1.25em;
    }
    &__wrap{
      margin-top: 1.75em;
      grid-template-columns: 100%;
      gap: 1.75em;
    }
    &__left{
      order: 2;
    }
    blockquote{
      margin: 2.5em 0;
    }
  }

  .about-slider{
    .swiper{
      &-wrapper{
        margin: 0;
      }
      &-slide{
        width: 20.75em;
        height: 15em;
        padding-right: .75em;
      }
      &-pagination{
        display: none;
      }
    }
  }

  .about-car{
    gap: 2.5em;
  }

  .about-form{
    margin-top: 3.75em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .about-table{
    margin-top: 5em;
    @include fullWith;
  }

  .about-hints{
    gap: 3.625em;
  }

  .about-hint{
    padding: 2.375em 1.75em 2.125em;
    &__img{
      width: calc(100% + 3.5em);
      margin-left: -1.75em;
    }
  }

  .contacts{
    margin-top: 12.5em;
    &__wrap{
      max-width: unset;
      width: calc(100% + 1.25em);
      left: -.625em;
      top: -6.25em;
      transform: unset;
      padding: 2em 1.25em;
    }
    &__items{
      margin-top: 2em;
    }
  }

  .map{
    @include fullWith;
    padding: 0;
    height: 31.25em;
    &__wrap{
      margin: 0;
      width: 100%;
    }
  }

  .qa{
    margin-top: 5em;
    &__items{
      margin-top: 1.75em;
      gap: 1em;
    }
  }

  .qa-card{
    &__show{
      padding: .75em 1.25em;
    }
    &__title{
      font-size: .875em;
    }
    &__hidden{
      width: calc(100% - 2.5em);
    }
  }
}
