@import "vars";
@mixin font-face($name, $file, $weight: 400, $style: normal) {
    @font-face {
        font-family: "#{$name}";
        src: url('../fonts/#{$file}.ttf') format('ttf'),
        url('../fonts/#{$file}.woff2') format('woff2'),
        url('../fonts/#{$file}.woff') format('woff'),
        url('../fonts/#{$file}.eot') format('eot');
        font-weight: $weight;
        font-style: $style;
        font-display: swap;
    }
}

@mixin scroll {
    &::-webkit-scrollbar {
        width: 3px;
    }
    &::-webkit-scrollbar-track {
        background: $yellow;
        border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
        background: $black;
        border-radius: 3px;
    }
}

@mixin imgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@mixin bgImgSize($size) {
    background-size: $size auto;
    background-repeat: no-repeat;
    background-position: center;
}

@mixin center {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin absolute {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin grid100gap($gap) {
    display: grid;
    grid-template-columns: 100%;
    gap: $gap;
}

@mixin grid2 {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - .625em));
    gap: 1.875em 1.25em;
    @media screen and (max-width: 576px) {
        grid-template-columns: 100%;
        gap: 1.5em;
    }
}

@mixin grid3 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
    gap: 1.75em 1.25em;
    @media screen and (max-width: 992px) {
        grid-template-columns: 100%;
        @media screen and (min-width: 577px) {
            grid-template-columns: repeat(2, calc(50% - .625em));
        }
    }
}

@mixin linearText {
    background-image: $linear2;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

@mixin fullWith {
    width: calc(100% + 2.5em);
    padding: 0 1.25em 1em;
    margin-left: -1.25em;
}

@mixin card {
    background: #FFFFFF;
    box-shadow: 1px 1px 31px rgba(43, 42, 124, 0.07);
    border-radius: .3125em;
}

@mixin fullScroll {
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    padding: 0 1.25em;
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}