@import "../mixins";
.blog{
  padding-bottom: 7.5em;
  background-color: $bg;
  flex-grow: 1;
  position: relative;
  overflow-x: hidden;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    right: 100%;
    transform: translateX(calc(((100vw - 1200px) / 2) + 3.75em + 16.9em));
    width: 3000px;
    height: 100%;
    background-color: #fff;
    pointer-events: none;
  }
  &__inner{
    display: grid;
    grid-template-columns: 17.8125em calc(100% - 17.8125em - 7.5em);
    justify-content: space-between;
    margin-top: 2.25em;
    position: relative;
    z-index: 2;
  }
  &__sidebar{
    position: relative;
    height: 100%;
    &-inner{
      height: 100%;
    }
    .article-content{
      padding: 1.75em 1.5em 2em;
      position: sticky;
      top: 1.25em;
      z-index: 3;
      &__link{
        font-weight: 400;
      }
    }
  }
  .menu-form{
    &__title{
      color: $black;
    }
  }
  &__info{
    @include grid100gap(1.75em);
  }
  &__model{
    margin-top: 2.25em;
    display: flex;
    align-items: center;
    .fg{
      margin-left: 1.875em;
      flex-grow: 1;
      max-width: 24.1875em;
    }
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .75em);
    margin-top: .5em;
    &-title{
      display: none;
    }
  }
  &__tag{
    height: 2.125em !important;
    padding: 0 .75em;
    margin: 1em .75em 0 0;
    background-color: $bg;
    border-radius: .3125em;
    span{
      font-size: .875em;
      font-weight: 300;
    }
  }
  &__groups{
    margin-top: 5em;
    @include grid100gap(8.75em);
  }
  &__info2{
    @include grid100gap(1.75em);
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__grid{
    @include grid2;
    margin-top: 2.5em;
  }
}

.blog-banner{
  margin: 5em auto 0;
  width: 15em;
  display: block;
  img{
    width: 100%;
    display: block;
  }
  &.mt-0{
    margin-top: 0;
  }
}

.experts{
  margin-top: 8.75em;
  &__title{
    text-align: center;
  }
  &__items{
    margin-top: 3.75em;
    @include grid100gap(6.875em);
  }
}

.expert-card{
  &__img{
    position: relative;
    padding-bottom: 100%;
    margin-top: 1.25em;
    img{
      @include imgCover;
    }
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
  &__item{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: .25em;
    font-family: $font2;
    &-title,
    &-desc{
      font-size: .875em;
      line-height: 140%;
    }
    &-title{
      font-weight: 700;
    }
    &-desc{
      text-align: right;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.75em;
  }
}

.blog-collection{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__grid{
    margin-top: 1.75em;
    @include grid2;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2.125em;
  }
  &__btn{
    padding: 0 1em;
    width: 24.1875em;
    max-width: 100%;
  }
}

.blog-filter{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr .87fr;
  gap: .75em;
  margin-top: 1.75em;
  .fg{
    .current,
    input{
      height: 2.5714em;
      font-size: .875em;
      border-radius: .57em;
    }
  }
  &__btn{
    height: 2.25em !important;
    span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 1280px){
  .blog{
    &:before{
      transform: translateX(calc(2.5em + 21.5625em));
    }
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding: 2.75em 0 8.75em;
    &:before{
      display: none;
    }
    &__inner{
      margin: 0;
      grid-template-columns: 100%;
      gap: 6.25em;
    }
    &__content{
      order: -1;
    }
    &__sidebar{
      &--mob-hide{
        display: none;
      }
    }
    .menu-form{
      display: none;
    }
    &__info{
      gap: 1.25em;
    }
    &__model{
      margin-top: 2em;
      display: block;
      .fg{
        width: 100%;
        margin: .5em 0 0 0;
      }
    }
    &__tags{
      margin-top: -.25em;
      &-title{
        margin-top: 1.5em;
      }
    }
    &__tag{
      margin-top: 1em;
      padding: 0 .5em;
      span{
        font-size: .75em;
      }
    }
    &__groups{
      gap: 5em;
    }
    &__info2{
      gap: 1.25em;
      padding-bottom: 3.875em;
      position: relative;
    }
    &__subscribe{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
    }
    &__grid{
      margin-top: 3.75em;
    }
  }

  .blog-banner{
    display: none;
  }

  .experts{
    margin-top: 0;
    &__items{
      margin-top: 1.75em;
      gap: 3.5em;
      @media screen and (min-width: 577px){
        grid-template-columns: 1fr 1fr;
        gap: 1.5em;
        .expert-card{
          &__title{
            min-height: 3.15em;
            display: block;
          }
        }
      }
    }
  }

  .blog-collection{
    &__subscribe{
      display: none;
    }
    &__nav{
      margin-top: 1.75em;
    }
  }

  .blog-filter{
    grid-template-columns: 100%;
    &__btn{
      margin-top: .5em;
    }
  }
}
