@import "../mixins";
.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;

    @media screen and (min-width: 577px) {
        text-align: center;
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
        .modal__dialog {
            display: inline-block;
            text-align: left;
            vertical-align: middle;
        }
    }

    &--active {
        opacity: 1;
        pointer-events: unset;
    }

    &__bg {
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        background: rgba(104, 104, 104, 0.7);
        backdrop-filter: blur(2.5px);
    }

    &__dialog {
        margin: auto;
        background-color: $bg;
        width: 36.875em;
        max-width: calc(100% - 2em);
        padding: 3.75em 3.125em;
        position: relative;
        border-radius: 0;
    }

    &__close {
        font-size: 1em;
        position: absolute;
        top: 2.25em;
        right: 2.25em;
        width: 1.5em;
        height: 1.5em;
        img {
            width: 100%;
            transition: all .3s;
        }
        &:hover {
            img {
                opacity: .6;
            }
        }
    }
    &__title{
        text-align: center;
    }
    &__form{
        @include grid100gap(1.25em);
        margin-top: 1.5em;
    }
    &__captcha{
        display: flex;
        align-items: flex-end;
        .captcha{
            height: 3.125em;
            margin-left: 1em;
            flex-shrink: 0;
            &>img{
                width: 10.75em;
                margin-right: .5em;
            }
        }
        .fg{
            .fg__error-text{
                position: absolute;
                top: 100%;
                left: 0;
                width: 130%;
                margin: 0;
            }
        }
    }
    &__check{
        margin-top: 1.5em;
    }
    &__btn{
        width: 100%;
    }
    &__info{
        @include grid100gap(.5em);
    }
    &__resend{
        margin-top: .75em;
        text-decoration-line: underline;
        &:hover{
            text-decoration: none;
        }
    }
}

.modal-small{
    @media screen and (min-width: 577px){
        .modal__dialog{
            padding: 2.5em 1.75em 4em;
            width: 24.125em;
        }
        .modal__close{
            top: 1em;
            right: 1em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            overflow-y: auto;
            max-width: unset;
            padding: 4.5em 1.25em;
        }
        &__captcha{
            flex-direction: column-reverse;
            align-items: center;
            .captcha{
                margin: 0 0 1em;
                img{

                }
            }
            .fg{
                width: 100%;
                .fg__error-text{
                    width: 100%;
                }
            }
        }
        &__close{
            top: 1.75em;
            right: 1.25em;
        }
        &__info{
            gap: 2.5em;
        }
    }

    .modal-small{
        .modal__dialog{
            padding-top: 8em;
        }
        .modal__btn{
            margin-top: 2.5em;
        }
    }
}
