@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";

.tabs{
  margin-top: .5em;
  display: flex;
  border-bottom: $border;
}

._tab{
  padding: .5em 0;
  color: $grey;
  font-family: $font2;
  flex-shrink: 0;
  position: relative;
  transition: all .3s;
  cursor: pointer;
  &:before{
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $lightGrey;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: .875em;
    line-height: 140%;
    text-transform: uppercase;
  }
  &:not(:last-child){
    margin-right: 2.5em;
  }
  &._active{
    pointer-events: none;
  }
  &._active,
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .tabs{
    overflow-x: auto;
    @include fullScroll;
  }
}
