.conf{
  &__content{
    font-family: $font2;
    margin-top: 1.5em;
    p{
      font-size: .875em;
      line-height: 140%;
      &:not(:last-child){
        margin-bottom: 1.5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .conf{
    &__content{
      margin-top: 1.75em;
    }
  }
}
