@import "../mixins";
.article{
  &__top{
    @include grid100gap(1em);
  }
  &__expert{
    margin-top: 3.75em;
    max-width: 36.875em;
    .expert-card {
      &__wrap{
        display: grid;
        grid-template-columns: 11.4375em auto;
        align-items: center;
        gap: 1.25em;
        margin-top: 1.75em;
      }
      &__img{
        margin: 0;
      }
      &__items{
        margin-top: 0;
      }
      &__item{
        @media screen and (min-width: 993px){
          &-title,
          &-desc{
            font-size: 1.0625em;
          }
        }
        &-title{
          font-weight: 500;
        }
      }
    }
  }
  &__desc{
    margin-top: 3.6em;
    p{
      line-height: inherit;
    }
  }
}

.article-content{
  margin-top: 3.75em;
  max-width: 24.125em;
  width: 100%;
  padding: 2em 1.75em 2.25em;
  background: #FFFFFF;
  border: $border;
  border-radius: .5em;
  &._hide{
    .article-content__btn{
      .view1{
        display: none;
      }
      .view2{
        display: unset;
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    color: $grey;
    text-decoration-line: underline;
    .view2{
      display: none;
    }
    &:hover{
      text-decoration: none;
    }
  }
  &__bottom{
    margin-top: 1.75em;
  }
  &__links{
    @include grid100gap(.5em);
    counter-reset: count;
  }
  &__link{
    counter-increment: count;
    display: flex;
    font-weight: 300;
    &:before{
      content: counter(count) ' . ';
      margin-right: .25em;
      flex-shrink: 0;
    }
  }
}

.article-chapter{
  margin-top: 3.75em;
  h3{
    font-weight: 600;
    margin-bottom: .91em;
  }
  p,
  ul{
    line-height: 140%;
    &:not(:last-child){
      margin-bottom: 1.4em;
    }
  }
  ul,
  h5,
  p{
    font-family: $font2;
    font-weight: 300;
    max-width: 40.7em;
  }
  ul{
    margin-top: 1.2em;
  }
  h5{
    font-weight: 500;
  }
}

.article-chapter,
.about{
  blockquote{
    margin-top: 5em;
    padding: 1.75em;
    max-width: 43.1875em;
    p{
      position: relative;
      z-index: 2;
    }
  }
}

.gallery{
  margin: 3.75em 0;
  &__big{
    a{
      display: block;
      padding-bottom: 75%;
      position: relative;
      img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__small{
    margin-top: .75em;
    position: relative;
    .swiper{
      &-button{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 1.5em;
        font-size: 1em;
        margin: 0;
        img{
          width: 100%;
          display: block;
        }
      }
      &-slide{
        width: 9.5625em;
        height: 5.9375em;
        padding-right: .75em;
        cursor: pointer;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: .3125em;
        }
      }
    }
  }
  .swiper-slide{
    img{

    }
  }
}

.gallery2{
  margin: 3.75em 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.25em;
  &__col{
    &:nth-child(1){
      grid-column: 1/-1;
      .gallery2__img{
        padding-bottom: 50%;
      }
    }
  }
  &__img{
    padding-bottom: 76%;
    position: relative;
    display: block;
    img{
      @include imgCover;
    }
  }
}

.m-table{
  margin-top: 1.25em;
  overflow-x: auto;
  font-family: $font2;
  table{
    border-spacing: 0;
    border-collapse: collapse;
    min-width: 43.125em;
  }
  th{
    font-weight: 500;
    color: $accent2;
    height: 3.875em;
    border-top: $border;
  }
  th,
  td{
    //width: 14.375em;
    text-align: center;
    background: #FFFFFF;
    border-bottom: $border;
    &:nth-child(1){
      border-left: $border;
    }
    &:last-child{
      border-right: $border;
    }
  }
  td{
    height: 3.75em;
  }
  tbody{
    tr{
      &:nth-child(odd){
        td{
          background-color: $bg;
        }
      }
    }
  }
}

.article-slider{
  position: relative;
  max-width: 43.1875em;
  &__img{
    position: relative;
    padding-bottom: 75%;
    display: block;
    img{
      @include imgCover;
    }
  }
  &__text{
    margin-top: .75em;
    p{
      max-width: 100%;
      font-size: .875em;
    }
  }
  .swiper{
    &-button{
      transform: translateY(-3em);
      font-size: 1em;
      width: 1.5em;
      svg{
        width: 100%;
        height: 1.5em;
        fill: $grey;
      }
      &-prev{
        left: -2.5em;
      }
      &-next{
        right: -2.5em;
      }
    }
    &-pagination{
      position: absolute;
      top: .75em;
      right: .75em;
      bottom: unset;
      left: unset;
      width: unset;
      height: 1.89em;
      border-radius: .9375em;
      font-size: 1.125em;
      color: $accent2;
      background-color: rgba(255, 255, 255, 0.7);
      padding: 0 .67em;
      z-index: 2;
      display: flex;
      align-items: center;
      font-weight: 600;
      font-variant-numeric: tabular-nums;
      -webkit-font-feature-settings: "tnum";
      font-feature-settings: "tnum";
    }
  }
}

.opinion{
  margin-top: 5.75em;
  padding: 2.5em 0 2em;
  position: relative;
  display: flex;
  &:before{
    content: '';
    position: absolute;
    height: 100%;
    width: 10000px;
    background: #E8EDFF;
    top: 0;
    left: -3.75em;
  }
  &__left,
  &__right{
    position: relative;
    z-index: 2;
  }
  .avatar{
    margin-right: 1.25em;
  }
  &__left{
    display: flex;
    flex-shrink: 0;
  }
  &__right{
    margin-left: 1.875em;
    max-width: 24.125em;
  }
  &__subtitle{
    margin: .25em 0;
  }
  &__badge{
    position: absolute;
    left: 0;
    top: 0;
    transform: translateY(-50%);
    z-index: 2;
    padding: 0 .75em;
    box-shadow: 1px 1px 31px rgba(43, 42, 124, 0.08);
    border-radius: .3125em;
    background-image: $linear2;
    color: #FFFFFF;
    height: 1.875em;
    display: flex;
    align-items: center;
  }
}

.avatar{
  width: 6.25em;
  height: 6.25em;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }
}

.opinion2{
  padding: 2em 2.25em;
  background-color: #fff;
  box-shadow: 1px 1px 31px rgba(43, 42, 124, 0.07);
  border-radius: .3125em;
  margin-top: 5em;
  position: relative;
  .avatar{
    float: left;
    margin-right: 1.5em;
  }
  &__text{
    max-width: 31.25em;
    min-height: 6.25em;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__quote{
    position: absolute;
    right: 1.5em;
    bottom: 0;
    transform: translateY(50%);
    width: 4.25em;
  }
}

.plus-minus{
  margin-top: 6em;
  @include grid2;
  &__group{
    padding: 2em;
    @include card;
  }
  &__img{
    width: 2em;
    display: block;
    margin: 0 auto 1.25em;
  }
}

.opinion3{
  margin-top: 5.5em;
  padding: 1.75em 2em 1.75em 2.25em;
  @include card;
  &__wrap{
    padding: 1.25em 0 1.25em 1.5em;
    border-left: $border;
  }
  &__text{
    p{
      line-height: inherit;
    }
  }
}

.article-width{
  max-width: 43.1875em;
}

.most-read{
  margin-top: 5em;
  background: #FFFFFF;
  box-shadow: 1px 1px 31px rgba(43, 42, 124, 0.07);
  display: grid;
  grid-template-columns: 12.6875em calc(100% - 12.6875em);
  &__img{
    img{
      width: 100%;
    }
  }
  &__content{
    padding: .75em 1.75em;
  }
  &__info{
    @include grid100gap(.25em);
  }
}

.socials{
  display: flex;
  align-items: center;
  margin-top: 3.75em;
  &__btn{
    width: 1.5em;
    margin-right: 1em;
    &:hover{
      img{
        transform: translateY(-1em);
      }
    }
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
}

.quiz{
  max-width: 30.5em;
  background-color: #fff;
  border: 1px solid $accent2;
  border-radius: .3125em;
  padding: 3em 2.5em 4em;
  margin-top: 5em;
  position: relative;
  &._voted{
    .quiz__radios{
      display: none;
    }
    .quiz__votes{
      display: grid;
    }
  }
  &__top{
    @include grid100gap(.5em);
    margin-bottom: 2.25em;
  }
  &__radios,
  &__votes{
    @include grid100gap(1em);
  }
  &__votes{
    display: none;
  }
  &__vote{
    height: 2em;
    display: flex;
    align-items: center;
    border-radius: .3125em;
    background-color: $bg;
    position: relative;
    padding: 0 1.125em;
    &-fill{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      display: block;
      background: #DFE4EC;
      border-radius: inherit;
    }
    &-title,
    &-desc,
    &-percent{
      position: relative;
      z-index: 2;
    }
    &-title{
      width: 3em;
    }
    &-percent{
      margin-left: auto;
    }
  }
  &__count{
    position: absolute;
    bottom: 1.5em;
    left: 2.5em;
  }
}

.other{
  max-width: 30.5em;
  background-color: #fff;
  border: 1px solid $accent2;
  border-radius: .3125em;
  padding: 2em 1.75em;
  margin-top: 5em;
  @include grid100gap(1.25em);
}

.reactions{
  margin-top: 3.75em;
  display: flex;
  align-items: center;
  &__btn{
    display: flex;
    align-items: center;
    font-size: 1em;
    &:not(:last-child){
      margin-right: 2.5em;
    }
    &-text{
      font-variant-numeric: tabular-nums;
      -webkit-font-feature-settings: "tnum";
      font-feature-settings: "tnum";
    }
    ._view2{
      display: none;
    }
    svg{
      width: 2em;
      height: 2em;
      margin-right: .75em;
      fill: $grey;
    }
    .text18{
      display: flex;
      align-items: center;
    }
    &:hover{
      svg{
        fill: $black
      }
      .grey-text{
        color: $black;
      }
    }
    &._like{
      &._active{
        .grey-text{
          color: $green;
        }
        svg{
          fill: $green;
        }
      }
    }
    &._unlike{
      &._active{
        .grey-text{
          color: $red;
        }
        svg{
          fill: $red;
        }
      }
    }
    &._favourite{
      &._active{
        ._view1{
          display: none;
        }
        ._view2{
          display: unset;
        }
      }
    }
  }
}

.comments{
  margin-top: 5em;
  &__count{
    display: block;
    margin-top: 3em;
    width: 2.5em;
  }
  &__items{
    margin-top: 1.75em;
    @include grid100gap(2.5em);
  }
  &__more{
    margin-top: 1em;
    padding-top: 1.25em;
    border-top: $border;
    display: flex;
    align-items: center;
    justify-content: center;
    &-link{
      display: flex;
      align-items: center;
    }
    &-images{
      display: flex;
      align-items: center;
      margin-right: 1em;
      img{
        border-radius: 50%;
        width: 2em;
        height: 2em;
        display: block;
        &:not(:first-child){
          margin-left: -.75em;
        }
      }
    }
  }
}

.new-comment{
  @include grid100gap(1.25em);
}

.comment-field{
  position: relative;
  &._error{
    margin-bottom: 1.875em;
  }
  &__nav{
    position: absolute;
    right: 1em;
    bottom: 1em;
    display: flex;
    align-items: flex-end;
  }
  &__picture{
    width: 1.5em;
    position: relative;
    cursor: pointer;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      z-index: 2;
      cursor: pointer;
      &:hover{
        &+svg{
          opacity: 1;
        }
      }
    }
    svg{
      width: 100%;
      height: 1.5em;
      opacity: .4;
    }
  }
  &__btn{
    margin-left: 1em;
    display: none;
  }
  &._show{
    .comment-field__btn{
      display: flex;
    }
  }
  textarea{
    padding-bottom: 4em;
  }
  .fg__error-text{
    position: absolute;
    top: 100%;
    left: 0;
  }
}

.comment-card{
  display: flex;
  &._expanded{
    .comment-card__toggle{
      ._view1{
        display: none;
      }
      ._view2{
        display: unset;
      }
      img{
        transform: rotate(180deg);
      }
    }
  }
  .avatar{
    width: 3em;
    height: 3em;
    flex-shrink: 0;
    margin-right: 1em;
  }
  &__content{
    flex-grow: 1;
    width: calc(100% - 4em);
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__status{
    display: flex;
    align-items: center;
    margin-left: 1em;
    flex-shrink: 0;
    padding: 0 .75em;
    background: #FFFFFF;
    border-radius: .625em;
    height: 1.5em;
    img{
      width: 1em;
      margin-right: .5em;
    }
  }
  &__date{
    margin-left: 1em;
  }
  &__text{
    margin-top: .75em;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 1em;
      }
    }
  }
  &__toggle{
    margin: .75em 0 0 auto;
    display: flex;
    align-items: center;
    font-size: 1em;
    img{
      width: .75em;
      margin-left: .5em;
      display: block;
      transition: inherit;
    }
    ._view2{
      display: none;
    }
    &:hover{
      opacity: .8;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: .75em;
    .reactions__btn{
      svg{
        width: 1.5em;
        height: 1.5em;
      }
      &:not(:last-child){
        margin-right: 1.75em;
      }
    }
    ._like{
      .grey-text{
        color: $green !important;
      }
    }
    ._unlike{
      .grey-text{
        color: $red !important;
      }
    }
  }
  &__answer{
    margin-right: 1.75em;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
  .comment-field{
    margin-top: 1em;
    display: none;
    textarea{
      min-height: 5.33em;
      padding: .67em .89em 4em;
    }
  }
}

.comment-subgroup{
  padding: 2em 0 0 4em;
  display: none;
  .comment-card{
    &:not(:last-child){
      margin-bottom: 1.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .article{
    &__top{
      gap: 1em;
    }
    &__expert{
      margin-top: 2em;
      .expert-card{
        &__wrap{
          grid-template-columns: 5.875em calc(100% - 5.875em - .75em);
          gap: .75em;
          margin-top: 1em;
        }
        &__items{
          gap: .375em;
        }
        &__item{
          gap: unset;
          display: flex;
          justify-content: space-between;
          &-desc{
            font-weight: 300;
          }
        }
      }
    }
    &__desc{
      margin-top: 2em;
    }
  }

  .article-content{
    margin-top: 2.75em;
    padding: 1.75em 1.5em;
    .display4{
      font-size: 1.25em;
    }
    &__link{
      span{
        text-decoration: underline;
      }
    }
  }

  .article-chapter,
  .about{
    blockquote{
      padding: 1.5em 1.25em;
      margin-top: 2.5em;
    }
  }

  .gallery{
    margin: 2em 0;
    &__big{
      overflow: unset;
      .swiper{
        &-slide{
          width: 20.75em;
          padding-right: .75em;
        }
      }
    }
    &__small{
      display: none;
    }
  }

  .gallery2{
    gap: .5em;
    margin: 2em 0;
  }

  .m-table{
    @include fullWith;
  }

  .article-slider{
    @include fullWith;
    padding: 0;
    .swiper{
      &-button{
        margin-top: 0;
        top: 11em;
        height: 1.5em;
        svg{
          fill: #fff;
        }
        &-prev{
          left: 1.25em;
        }
        &-next{
          right: 1.25em;
        }
      }
      &-pagination{
        right: 1.176em;
      }
    }
    &__text{
      padding: 0 1.176em;
    }
  }

  .opinion{
    padding-bottom: 2.5em;
    display: block;
    margin-top: 4em;
    &__right{
      margin: .75em 0 0;
    }
  }

  .opinion2{
    padding: 1.75em 1.25em;
    margin-top: 3.75em;
    .avatar{
      margin: 0 .75em 0 .5em;
    }
    &__text{
      display: block;
    }
    &__quote{
      right: 1.25em;
      width: 3.125em;
    }
  }

  .plus-minus{
    margin-top: 3.75em;
    &__group{
      padding: 1.25em;
    }
  }

  .opinion3{
    margin-top: 3.75em;
    padding: 1.75em 1.25em;
    &__wrap{
      padding: 0 0 0 1em;
    }
  }

  .socials{
    margin-top: 2em;
  }
}


@media screen and (max-width: 576px){
  .most-read{
    margin-top: 5.5em;
    @include fullWith;
    grid-template-columns: 100%;
    gap: 1.25em;
    padding: 1.25em;
    &__content{
      padding: 0;
    }
  }

  .quiz{
    padding: 1.75em 1.25em;
    margin-top: 3.75em;
    &__top{
      margin-bottom: 1.75em;
    }
    &__count{
      position: static;
      margin-top: .25em;
    }
  }

  .other{
    margin-top: 3.75em;
    padding: 1.35em 1.25em;
    gap: 1em;
  }

  .reactions{
    margin-top: 1.5em;
    &__btn{
      svg{
        width: 1.5em;
        height: 1.5em;
        margin-right: .5em;
      }
      &:not(:last-child){
        margin-right: 2em;
      }
    }
  }

  .comments{
    &__count{
      margin-top: 2.5em;
    }
    &__items{
      gap: 3em;
    }
    &__more{
      padding-top: 1em;
    }
  }

  .comment-field{
    &__nav{
      right: unset;
      left: .75em;
    }
  }

  .comment-card{
    &__top{
      justify-content: space-between;
      position: relative;
      padding-bottom: 1.5em;
    }
    &__name{
      max-width: 10.625em;
      word-break: break-word;
      width: calc(100% - 8em);
      font-size: .8125em;
    }
    &__date{
      margin: 0;
      position: absolute;
      left: 0;
      bottom: 0;
    }
    &__status{
      max-width: 7.5em;
      margin-left: .5em;
    }
    .reactions__btn{
      svg{
        width: 1em;
        height: 1em;
        margin-right: .75em;
      }
    }
  }

  .comment-subgroup{
    padding-left: .75em;
  }
}
