@import "../mixins";
.autoexperts{
  &__top{
    @include grid100gap(.75em);
  }
  &__desc{
    max-width: 24.5em;
  }
  &__grid{
    @include grid2;
    margin-top: 2.5em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3em;
  }
  &__btn{
    width: 24.1875em;
    max-width: 100%;
  }
}

.expert-card2{
  @include card;
  padding: 0;
  .expert-card{
    &__img{
      margin: 0;
      padding-bottom: 75%;
    }
  }
  &__content{
    padding: 1.25em 2em 2.25em;
  }
}

.expert{
  &__top{
    display: flex;
    justify-content: space-between;
    &--center{
      align-items: center;
    }
  }
  &__desc{
    max-width: 11.25em;
  }
  .subscribe-card__nav{
    margin: .5em 0 0;
    padding: 0;
    text-align: right;
  }
  .subscribe-card__cancel{
    margin: 0;
  }
}

.expert-card3{
  margin: 2.5em 0 3.75em;
  @include card;
  padding: 0;
  display: grid;
  grid-template-columns: 24.125em calc(100% - 24.125em);
  .expert-card{
    &__img{
      min-height: 100%;
      margin: 0;
      padding-bottom: 75%;
      img{
        border-radius: .3125em 0 0 .3125em;
      }
    }
    &__items{
      margin: 0;
      gap: 1.25em;
    }
    &__item{
      &-title,
      &-desc{
        font-size: 1.0625em;
      }
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1.5em;
  }
}


@media screen and (max-width: 992px){
    .expert-card3{
        grid-template-columns: 100%;
        .expert-card{
          &__items{
            gap: .75em;
          }
          &__item{
            &-title,
            &-desc{
              font-size: .875em;
            }
          }
        }
        &__content{
          padding: 1.75em 1.25em;
        }
    }
}


@media screen and (max-width: 576px){
  .autoexperts{
    &__top{
      gap: .75em;
    }
    &__grid{
      gap: 2.5em;
      margin-top: 3.75em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .expert-card2{
    &__content{
      padding: 1.25em 1.25em 1.75em;
    }
  }

  .expert{
    &__top{
      display: block;
    }
    &__content{
      margin-top: 1em;
    }
    .subscribe-card__nav{
      margin: .25em 0 0;
      text-align: left;
    }
  }
}
